@import './mixins.scss';
@import './vars.scss';

html {
    font-size: 62.5%; // 1rem is 10px
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

body {
    overflow-x: hidden;
    font-weight: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    -webkit-tap-highlight-color: transparent;
}
.hidden{
    display: none;
}
.app{
    @include flexbox-container($justify-content: center, $align-items: center);
}
.btn{
    @include welcome-buttons();
}